.ImageGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(
    auto-fill,
    minmax(362px, 1fr)
  );
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.loader {
  display: block;
  width: 40px;
  margin-left: auto;
  margin-right: auto;
}
